<template>
    <div class="page py-4">
        <h3>Automatos Finitos NÃ£o-determinÃ­sticos</h3>

        <b-alert show variant="danger" v-if="error">
            {{ error }}
        </b-alert>

        <b-table-lite :items="items" v-if="items.length > 0">
            <template #head(_) />
            <template #head()="data">
                <div class="text-center cursor-pointer" @click="estadoOptions(items_map.get(data.field.key))">
                    <font-awesome-icon icon="arrow-right" class="text-success" v-if="inicial == data.field.key" />
                    <font-awesome-icon :icon="['far', 'circle']" class="ml-1 text-danger" v-if="finais.includes(data.field.key)" />
                    {{ data.field.key }}
                </div>
            </template>

            <template #cell(_)="data">
                <b-form-input class="x-input" v-model="data.value" readonly autocomplete="off" tabindex="-1" />
            </template>
            <template #cell()="data">
                <b-form-input class="x-input" v-model="items[data.index][data.field.key]" trim autocomplete="off" />
            </template>
        </b-table-lite>
        <b-alert show v-else variant="secondary">
            Clique em <b>Novo estado</b> para comeÃ§ar.
        </b-alert>

        <div class="d-flex flex-column">
            <b-button variant="outline-primary" class="mb-1" @click="novoEstado">Novo estado</b-button>
            <b-button variant="primary" class="mb-1" @click="simular" v-if="items.length > 0">Simular</b-button>
            <b-button variant="primary" @click="converterAFD" v-if="items.length > 0">Converter para AFD</b-button>
        </div>

        <div v-if="resultado" class="pt-4 w-100">
            <hr>
            <h4 class="text-center">Resultado da simulaÃ§Ã£o:</h4>
            <hr class="w-100">

            <div class="row w-100 no-gutters">
                <div class="col-4 d-flex flex-column align-items-center px-2">
                    <h5>Caminhos:</h5>
                    <div class="d-flex mb-1 w-100 bg-light" v-for="c in resultado.caminhos" :key="c._i">
                        <code class="mr-2 bg-dark px-2">
                            <span v-for="s in c.sequencia" :key="s._i" class="mr-1" v-b-tooltip.hover :title="!s.t ? 'Inicial' : `-> ${s.t}`">
                                {{ s.s }}
                            </span>
                        </code>
                        <div class="text-success" v-if="c.aceita">
                            <font-awesome-icon icon="check" />
                        </div>
                        <div class="text-danger" v-else-if="c.indefinido" v-b-tooltip.hover :title="'-> ' + c.indefinido.c">
                            <font-awesome-icon icon="question" />
                        </div>
                        <div class="text-danger" v-else>
                            <font-awesome-icon icon="times" />
                        </div>
                    </div>
                </div>
                <div class="col-4 d-flex flex-column align-items-center px-2">
                    <h5>Geral</h5>
                    <div class="bg-light w-100 p-2 d-flex flex-column">
                        <b>Palavra:</b>
                        <code class="mb-2 text-info border border-info p-1" style="font-style: italic" v-b-tooltip.hover title="Palavra vazia" v-if="resultado.palavra.length === 0">Îµ</code>
                        <code class="mb-2 text-black border border-info p-1" v-else>{{ resultado.palavra }}</code>
                        <b>Aceita:</b>
                        <div v-if="resultado.aceita">
                            <span class="text-success"><font-awesome-icon icon="check" /></span> Sim
                        </div>
                        <div v-else>
                            <span class="text-danger"><font-awesome-icon icon="times" /></span> NÃ£o
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div v-if="afd_equivalente" class="pt-4 w-100">
            <hr>
            <h4 class="text-center">AFD equivalente:</h4>
            <hr class="w-100">

            <div class="row w-100 no-gutters">
                <div class="col-4 d-flex flex-column align-items-center px-2">
                    <h5>Texto plano</h5>
                    <pre class="bg-dark text-light p-2"><code><span v-for="r in (afd_equivalente.exibir_renomeado ? afd_equivalente.renomeado : afd_equivalente).afd_texto" :key="r">{{ r }}<br></span></code></pre>
                </div>
                <div class="col-4 d-flex flex-column align-items-center">
                    <b-table-lite :items="(afd_equivalente.exibir_renomeado ? afd_equivalente.renomeado : afd_equivalente).afd" :fields="afd_equivalente.afd_fields" v-if="afd_equivalente.afd.length > 0">
                        <template #head(__) />
                        <template #head(_)>
                            Î´
                        </template>

                        <template #head()="data">
                            {{ data.field.key }}
                        </template>
                        
                        <template #cell(__)="data">
                            <span v-if="afd_equivalente.exibir_renomeado">
                                <font-awesome-icon icon="arrow-right" class="ml-1 text-success" v-if="afd_equivalente.renomeado.inicial == data.item['_']" />
                                <font-awesome-icon :icon="['far', 'circle']" class="ml-1 text-danger" v-if="afd_equivalente.renomeado.finais.includes(data.item['_'])" />
                            </span>
                            <span v-else>
                                <font-awesome-icon icon="arrow-right" class="ml-1 text-success" v-if="Array.from(afd_equivalente.inicial).sort().join(',') == Array.from(data.item['_']).sort().join(',')" />
                                <font-awesome-icon :icon="['far', 'circle']" class="ml-1 text-danger" v-if="afd_equivalente.finais.map(x => Array.from(x).sort().join(',')).includes(Array.from(data.item['_']).sort().join(','))" />
                            </span>
                        </template>

                        <template #cell()="data">
                            <span v-if="afd_equivalente.exibir_renomeado">
                                {{ data.value }}
                            </span>
                            <span v-else-if="data.value.size > 0">
                                { {{ Array.from(data.value).sort().join(', ') }} }
                            </span>
                        </template>
                    </b-table-lite>
                </div>
                <div class="col-4 d-flex flex-column align-items-center px-2">
                    <h5>AÃ§Ãµes</h5>
                    <div class="d-flex flex-column">
                        <b-button variant="outline-primary" class="mb-1" @click="toggleRenomearAFD">
                            <span v-if="afd_equivalente.exibir_renomeado">Restaurar estados</span>
                            <span v-else>Renomear estados</span>
                        </b-button>
                        <b-button variant="outline-primary" class="mb-1" @click="abrirAFD">Abrir em nova guia</b-button>
                    </div>
                </div>
            </div>
        </div>

        <b-modal ref="novo-estado" title="Novo estado" size="sm" centered hide-footer no-fade>
            <b-form @submit.prevent="novoEstadoSave">
                <b-form-group label="Nome:">
                    <b-form-input autofocus v-model="modal.novo_estado.nome" trim autocomplete="off" />
                </b-form-group>
                <b-button type="submit" variant="primary" class="w-100">Adicionar</b-button>
            </b-form>
        </b-modal>

        <b-modal ref="estado-options" :title="'Estado: ' + (modal.estado_options.item && modal.estado_options.item['_']) || ''" size="sm" centered hide-footer no-fade>
            <div v-if="modal.estado_options.item">
                <b-button :disabled="inicial === modal.estado_options.item['_']" variant="outline-success" @click="setEstadoInicial(modal.estado_options.item)" class="w-100 mb-2">Definir como inicial</b-button>
                <b-button variant="outline-danger" @click="toggleEstadoFinal(modal.estado_options.item)" class="w-100">
                    <span v-if="!finais.includes(modal.estado_options.item['_'])">Definir como final</span>
                    <span v-else>Definir como nÃ£o-final</span>
                </b-button>
            </div>
        </b-modal>
        
        <b-modal ref="simular" title="Simular" size="sm" centered hide-footer no-fade>
            <b-form @submit.prevent="simularPressed">
                <b-form-group label="ConteÃºdo da fita (palavra):">
                    <b-form-input autofocus v-model="modal.simular.palavra" trim autocomplete="off" />
                </b-form-group>
                <b-button type="submit" variant="primary" class="w-100">Simular</b-button>
            </b-form>
        </b-modal>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                items_map: new Map(),
                items: [
                ],
                inicial: null,
                finais: [],
                modal: {
                    novo_estado: {
                        nome: '',
                    },
                    estado_options: {
                        item: null,
                    },
                    simular: {
                        palavra: '',
                    },
                },
                error: null,
                resultado: null,
                afd_equivalente: null,
            }
        },
        mounted(){
            document.title = 'AFN - Linguagens Formais';

            const p = JSON.parse(this.$route.query?.p || 'null');
            this.loadAutomato(p);
        },
        methods: {
            loadAutomato(p){
                if(p == null)
                    return;

                const map = new Map();
                for(let item of p.items)
                    map.set(item['_'], item);

                this.items_map = map;

                for(let key in p)
                    this[key] = p[key];
            },
            novoEstado(){
                this.modal.novo_estado.nome = '';
                this.$refs['novo-estado'].show();
            },
            novoEstadoSave(){
                const novo = this.modal.novo_estado.nome;
                if(!novo)
                    return;

                /** @type {string[]} */
                const estados = this.items.map(x => x['_']);
                if(estados.includes(novo))
                    return;
                estados.push(novo);

                const items = [];

                const map = new Map();
                for(let item of this.items)
                    map.set(item['_'], item);

                for(let s1 of estados){
                    const obj = { '_': s1 };
                    for(let s2 of estados)
                        obj[s2] = map.has(s1) ? map.get(s1)[s2] || '' : '';
                    items.push(obj);
                }

                map.clear();
                for(let item of items)
                    map.set(item['_'], item);

                this.items_map = map;
                this.items = items;

                this.novoEstado();
            },
            estadoOptions(item){
                this.modal.estado_options.item = item;
                this.$refs['estado-options'].show();
            },
            setEstadoInicial(item){
                this.inicial = item['_'];
                
                this.$refs['estado-options'].hide();
            },
            toggleEstadoFinal(item){
                const includes = this.finais.includes(item['_']);
                if(includes)
                    this.finais = this.finais.filter(x => x !== item['_']);
                else
                    this.finais.push(item['_']);

                this.$refs['estado-options'].hide();
            },
            simular(){
                this.error = null;

                try {
                    if(this.inicial == null)
                        throw new Error('Defina qual o estado inicial');

                    if(this.finais.length < 1)
                        throw new Error('Defina pelo menos um estado final');

                    this.modal.simular.palavra = '';
                    this.$refs['simular'].show();

                    this.error = null;
                }
                catch(ex){
                    this.resultado = null;
                    
                    console.error(ex);
                    this.error = ex.message;
                }
            },
            simularPressed(){
                this.resultado = null;
                this.error = null;
                this.$refs['simular'].hide();

                try {
                    const maquina = {};

                    for(let item of this.items){
                        maquina[item['_']] = {};
                        for(let k in item){
                            if(k !== '_' && k.length > 0 && item[k].length > 0){
                                for(let t of item[k].split(',').map(x => x.trim())){
                                    if(t.length > 0){
                                        if(maquina[item['_']][t] == null)
                                            maquina[item['_']][t] = [];
                                        maquina[item['_']][t].push(k);
                                    }
                                }
                            }
                        }
                    }

                    const palavra = this.modal.simular.palavra;
                    const caminhos = [];
                    
                    caminhos.push({ _i: caminhos.length, sequencia: [ { _i: 0, s: this.inicial, t: null } ], aceita: false, indefinido: null });

                    for(let c of palavra){
                        for(let caminho of Array.from(caminhos)){
                            if(caminho.indefinido)
                                continue;

                            const state = caminho.sequencia[caminho.sequencia.length - 1].s;
                            if(maquina[state][c] == null){
                                caminho.indefinido = { c };
                                continue;
                            }

                            const next_states = maquina[state][c];
                            const caminho_clone = JSON.parse(JSON.stringify(caminho));

                            for(let i = 0; i < next_states.length; i++){
                                const next_state = next_states[i];

                                const _caminho = i === 0 ? caminho : JSON.parse(JSON.stringify(caminho_clone));
                                _caminho.sequencia.push({ _i: _caminho.sequencia.length, s: next_state, t: c })

                                if(i > 0)
                                    caminhos.push(_caminho);
                            }
                        }
                    }

                    for(let caminho of caminhos){
                        if(caminho.indefinido)
                            continue;
                        const state = caminho.sequencia[caminho.sequencia.length - 1].s;
                        if(this.finais.includes(state))
                            caminho.aceita = true;
                    }

                    this.resultado = {
                        palavra,
                        caminhos,
                        aceita: caminhos.find(x => x.aceita),
                    };

                    this.error = null;
                }
                catch(ex){
                    console.error(ex);
                    this.error = ex.message;
                }
            },
            converterAFD(){
                try {
                    if(this.inicial == null)
                        throw new Error('Defina qual o estado inicial');

                    if(this.finais.length < 1)
                        throw new Error('Defina pelo menos um estado final');
                
                    const maquina = {};
                    for(let item of this.items){
                        maquina[item['_']] = { '_': item['_'] };
                        for(let k in item){
                            if(k !== '_' && k.length > 0 && item[k].length > 0){
                                for(let t of item[k].split(',').map(x => x.trim())){
                                    if(maquina[item['_']][t] == null)
                                        maquina[item['_']][t] = [];
                                    maquina[item['_']][t].push(k);
                                }
                            }
                        }
                    }

                    const afd = [];
                    const afd_indices = new Map();

                    const getKey = A => Array.from(A).sort().join(',');

                    {
                        const estado = { '_': new Set([ this.inicial ]) };
                        afd.push(estado);
                        afd_indices.set(getKey(estado['_']), afd.length - 1);
                    }
                    
                    for(let i = 0; i < afd.length; i++){
                        const estado = afd[i];

                        for(let j of estado['_']){
                            for(let k in maquina[j]){
                                if(k !== '_' && k.length > 0){
                                    if(estado[k] == null)
                                        estado[k] = new Set();

                                    for(let x of maquina[j][k])
                                        estado[k].add(x);
                                }
                            }
                        }

                        for(let k in estado){
                            if(k !== '_' && k.length > 0){
                                const key = getKey(estado[k]);
                                if(!afd_indices.has(key)){
                                    afd.push({ '_': estado[k] });
                                    afd_indices.set(key, afd.length - 1);
                                }
                            }
                        }
                    }

                    const textoFormat = A => '<' + Array.from(A).sort().join(' ') + '>';

                    this.afd_equivalente = {
                        inicial: afd[0]['_'],
                        finais: afd.filter(x => this.finais.find(y => x['_'].has(y))).map(x => x['_']),
                        afd,
                        afd_texto: afd.map(x => `${textoFormat(x['_'])}\t${Object.keys(x).filter(y => y !== '_').sort().map(y => `${y} -> ${textoFormat(x[y])}`).join('\t')}`),
                        afd_fields: ['__','_'].concat(Array.from(afd.flatMap(x => Object.keys(x)).reduce((acc, x) => acc.add(x), new Set())).filter(x => x !== '_').sort()),
                        exibir_renomeado: false,
                    };

                    const renomeado = {};
                    {
                        let c = 0;
                        const antigo_novo_map = new Map();
                        
                        for(let s of afd){
                            const key = getKey(s['_']);
                            antigo_novo_map.set(key, `p${c}`);
                            c += 1;
                        }

                        renomeado.inicial = antigo_novo_map.get(getKey(this.afd_equivalente.inicial));
                        renomeado.finais = this.afd_equivalente.finais.map(x => antigo_novo_map.get(getKey(x)));
                        renomeado.afd = afd.map(x => {
                            const y = {};
                            for(let k in x)
                                y[k] = antigo_novo_map.get(getKey(x[k]))
                            return y;
                        });
                        renomeado.afd_texto = renomeado.afd.map(x => `${x['_']}\t${Object.keys(x).filter(y => y !== '_').sort().map(y => `${y} -> ${x[y]}`).join('\t')}`)
                    }
                    this.afd_equivalente.renomeado = renomeado;

                    console.log(this.afd_equivalente);
                        
                    this.error = null;
                }
                catch(ex){
                    this.resultado = null;
                    
                    console.error(ex);
                    this.error = ex.message;
                }
            },
            toggleRenomearAFD(){
                this.afd_equivalente.exibir_renomeado = !this.afd_equivalente.exibir_renomeado;
            },
            abrirAFD(){
                const getKey = A => Array.from(A).sort().join(',');

                let afd;
                
                if(this.afd_equivalente.exibir_renomeado){
                    afd = JSON.parse(JSON.stringify(this.afd_equivalente.renomeado));
                }
                else
                {
                    afd = {};

                    const antigo_novo_map = new Map();
                    
                    for(let s of this.afd_equivalente.afd){
                        const key = getKey(s['_']);
                        antigo_novo_map.set(key, `{${key}}`);
                    }

                    afd.inicial = antigo_novo_map.get(getKey(this.afd_equivalente.inicial));
                    afd.finais = this.afd_equivalente.finais.map(x => antigo_novo_map.get(getKey(x)));
                    afd.afd = this.afd_equivalente.afd.map(x => {
                        const y = {};
                        for(let k in x)
                            y[k] = antigo_novo_map.get(getKey(x[k]))
                        return y;
                    });
                }

                const payload = {
                    inicial: afd.inicial,
                    finais: afd.finais,
                    items: [],
                };

                const estados = afd.afd.map(x => x['_']);
                for(let s of afd.afd){
                    const item = { '_': s['_'] };
                    for(let k in s){
                        if(k !== '_' && k.length > 0)
                            item[s[k]] = k;
                    }

                    // gera as propriedades que estao faltando
                    for(let _item of afd.afd)
                        item[_item['_']] = item[_item['_']] != null ? item[_item['_']] : '';

                    // reordena as propriedades
                    const _item = { '_': item['_'] };
                    for(let k of Object.keys(item).sort((a,b) => estados.indexOf(a) - estados.indexOf(b))){
                        if(k !== '_' && k.length > 0)
                            _item[k] = item[k];
                    }
                    payload.items.push(_item);
                }

                window.open(this.$router.resolve({ path: 'afd', query: { p: JSON.stringify(payload) } }).href, '_blank');
            }
        }
    };
</script>

<style scoped>
    .page {
        min-width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .x-input {
        text-align: center;
    }
    
    .cursor-pointer {
        cursor: pointer;
    }

    .text-black {
        color: #000;
    }
</style>