<template>
    <div class="page">
        <h3>Linguagens Formais</h3>
        <hr class="w-100">
        <p>Selecione um app abaixo para continuar.</p>
        <div class="d-flex flex-column">
            <b-button variant="outline-primary" class="mb-1 x-btn-page" v-for="page in pages" :key="page.path" @click="gotoPage(page)"> {{ page.name }} </b-button>
        </div>
    </div>
</template>

<script>
    export default {
        computed: {
            pages(){
                return this.$router.options.routes.filter(x => x.show_in_home);
            }
        },
        methods: {
            gotoPage(page){
                // this.$router.push({ path: page.path });
                window.open(this.$router.resolve({ path: page.path }).href, '_blank');
            }
        }
    }
</script>

<style scoped>
    .page {
        min-width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .x-btn-page {
        min-width: 8rem;
    }
</style>