import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import AFD from '../views/AFD.vue';
import AFN from '../views/AFN.vue';
import AFNe from '../views/AFNe.vue';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home,
        show_in_home: false,
    },
    {
        path: '/afd',
        name: 'AFD',
        component: AFD,
        show_in_home: true,
    },
    {
        path: '/afn',
        name: 'AFN',
        component: AFN,
        show_in_home: true,
    },
    {
        path: '/afne',
        name: 'AFNe',
        component: AFNe,
        show_in_home: true,
    },
]

const router = new VueRouter({
    routes,
});

export default router;