<template>
    <div class="page py-4">
        <h3>Automatos Finitos NÃ£o-determinÃ­sticos (Îµ)</h3>
        <p>Automatos Finitos NÃ£o-determinÃ­sticos com transiÃ§Ãµes vazias</p>

        <b-alert show variant="danger" v-if="error">
            {{ error }}
        </b-alert>

        <b-table-lite :items="items" v-if="items.length > 0">
            <template #head(_) />
            <template #head()="data">
                <div class="text-center cursor-pointer" @click="estadoOptions(items_map.get(data.field.key))">
                    <font-awesome-icon icon="arrow-right" class="text-success" v-if="inicial == data.field.key" />
                    <font-awesome-icon :icon="['far', 'circle']" class="ml-1 text-danger" v-if="finais.includes(data.field.key)" />
                    {{ data.field.key }}
                </div>
            </template>

            <template #cell(_)="data">
                <b-form-input class="x-input" v-model="data.value" readonly autocomplete="off" tabindex="-1" />
            </template>
            <template #cell()="data">
                <b-form-input class="x-input" v-model="items[data.index][data.field.key]" trim autocomplete="off" />
            </template>
        </b-table-lite>
        <b-alert show v-else variant="secondary">
            Escolha uma opÃ§Ã£o para comeÃ§ar.
        </b-alert>

        <div class="d-flex flex-column">
            <b-button variant="outline-primary" class="mb-1" @click="novoEstado">Novo estado</b-button>
            <b-button variant="outline-primary" class="mb-1" v-if="items.length == 0" @click="definirViaRegex">Definir via expressÃ£o regular</b-button>
            <b-button variant="primary" class="mb-1" @click="simular" v-if="items.length > 0">Simular</b-button>
            <b-button variant="primary" @click="converterAFN" v-if="items.length > 0">Converter para AFN</b-button>
        </div>

        <div v-if="resultado" class="pt-4 w-100">
            <hr>
            <h4 class="text-center">Resultado da simulaÃ§Ã£o:</h4>
            <hr class="w-100">

            <div class="row w-100 no-gutters">
                <div class="col-4 d-flex flex-column align-items-center px-2">
                    <h5>Caminhos:</h5>
                    <div class="d-flex mb-1 w-100 bg-light" v-for="c in resultado.caminhos" :key="c._i">
                        <code class="mr-2 bg-dark px-2">
                            <span v-for="s in c.sequencia" :key="s._i" class="mr-1" v-b-tooltip.hover :title="!s.t ? 'Inicial' : `-> ${s.t}`">
                                {{ s.s }}
                            </span>
                        </code>
                        <div class="text-success" v-if="c.aceita">
                            <font-awesome-icon icon="check" />
                        </div>
                        <div class="text-danger" v-else-if="c.indefinido" v-b-tooltip.hover :title="'-> ' + c.indefinido.c">
                            <font-awesome-icon icon="question" />
                        </div>
                        <div class="text-danger" v-else>
                            <font-awesome-icon icon="times" />
                        </div>
                    </div>
                </div>
                <div class="col-4 d-flex flex-column align-items-center px-2">
                    <h5>Geral</h5>
                    <div class="bg-light w-100 p-2 d-flex flex-column">
                        <b>Palavra:</b>
                        <code class="mb-2 text-info border border-info p-1" style="font-style: italic" v-b-tooltip.hover title="Palavra vazia" v-if="resultado.palavra.length === 0">Îµ</code>
                        <code class="mb-2 text-black border border-info p-1" v-else>{{ resultado.palavra }}</code>
                        <b>Aceita:</b>
                        <div v-if="resultado.aceita">
                            <span class="text-success"><font-awesome-icon icon="check" /></span> Sim
                        </div>
                        <div v-else>
                            <span class="text-danger"><font-awesome-icon icon="times" /></span> NÃ£o
                        </div>
                    </div>
                </div>
                <div class="col-4 d-flex flex-column align-items-center px-2">
                    <h5>Fecho Îµ</h5>
                    <div class="bg-light w-100 p-2 d-flex flex-column">
                        <div class="mb-2" v-for="state in resultado.fecho_epsilon" :key="state['_']">
                            <b>Estado:</b> {{state['_']}}<br>
                            <code class="mb-2 text-info border border-info p-1" style="font-style: italic" v-b-tooltip.hover title="Conjunto vazio" v-if="state.e.length === 0">Ã</code>
                            <code class="mb-2 text-black border border-info p-1" v-else>{ {{ state.e.join(', ') }} }</code>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div v-if="afn_equivalente" class="pt-4 w-100">
            <hr>
            <h4 class="text-center">AFN equivalente:</h4>
            <hr class="w-100">

            <div class="row w-100 no-gutters">
                <div class="col-4 d-flex flex-column align-items-center px-2">
                    <h5>Texto plano</h5>
                    <pre class="bg-dark text-light p-2"><code><span v-for="r in afn_equivalente.afn_texto" :key="r">{{ r }}<br></span></code></pre>
                </div>
                <div class="col-4 d-flex flex-column align-items-center">
                    <b-table-lite :items="afn_equivalente.afn" :fields="afn_equivalente.afn_fields" v-if="afn_equivalente.afn.length > 0">
                        <template #head(__) />
                        <template #head(_)>
                            Î´
                        </template>

                        <template #head()="data">
                            {{ data.field.key }}
                        </template>
                        
                        <template #cell(__)="data">
                            <font-awesome-icon icon="arrow-right" class="ml-1 text-success" v-if="afn_equivalente.inicial == data.item['_']" />
                            <font-awesome-icon :icon="['far', 'circle']" class="ml-1 text-danger" v-if="afn_equivalente.finais.includes(data.item['_'])" />
                        </template>

                        <template #cell(_)="data">
                            {{ data.value }}
                        </template>

                        <template #cell()="data">
                            {{ (data.value || []).join(',') }}
                        </template>
                    </b-table-lite>
                </div>
                <div class="col-4 d-flex flex-column align-items-center px-2">
                    <h5>AÃ§Ãµes</h5>
                    <div class="d-flex flex-column">
                        <b-button variant="outline-primary" class="mb-1" @click="abrirAFN">Abrir em nova guia</b-button>
                    </div>
                </div>
            </div>
        </div>

        <b-modal ref="novo-estado" title="Novo estado" size="sm" centered hide-footer no-fade>
            <b-form @submit.prevent="novoEstadoSave">
                <b-form-group label="Nome:">
                    <b-form-input autofocus v-model="modal.novo_estado.nome" trim autocomplete="off" />
                </b-form-group>
                <b-button type="submit" variant="primary" class="w-100">Adicionar</b-button>
            </b-form>
        </b-modal>

        <b-modal ref="definir-regex" title="Definir via expressÃ£o regular" size="md" centered hide-footer no-fade>
            <b-form @submit.prevent="definirViaRegexSave">
                <b-form-group label="ExpressÃ£o regular:" description="Importante destacar uma diferenÃ§a em relaÃ§Ã£o ao material didÃ¡tico: nele o operador + Ã© 'guloso', pois pega todos os vizinhos atÃ© esbarrar em um par de parÃªnteses. Aqui o operador + sÃ³ pega os vizinhos imediatos. Para replicar o mesmo comportamento Ã© necessÃ¡rio adicionar um par de parÃªnteses nos vizinhos imediatos, de modo a pegar tudo que estÃ¡ dentro dos parÃªnteses.">
                    <b-form-input autofocus v-model="modal.definir_regex.regex" trim autocomplete="off" />
                </b-form-group>
                <b-button type="submit" variant="primary" class="w-100">Continuar</b-button>
            </b-form>
        </b-modal>

        <b-modal ref="estado-options" :title="'Estado: ' + (modal.estado_options.item && modal.estado_options.item['_']) || ''" size="sm" centered hide-footer no-fade>
            <div v-if="modal.estado_options.item">
                <b-button :disabled="inicial === modal.estado_options.item['_']" variant="outline-success" @click="setEstadoInicial(modal.estado_options.item)" class="w-100 mb-2">Definir como inicial</b-button>
                <b-button variant="outline-danger" @click="toggleEstadoFinal(modal.estado_options.item)" class="w-100">
                    <span v-if="!finais.includes(modal.estado_options.item['_'])">Definir como final</span>
                    <span v-else>Definir como nÃ£o-final</span>
                </b-button>
            </div>
        </b-modal>
        
        <b-modal ref="simular" title="Simular" size="sm" centered hide-footer no-fade>
            <b-form @submit.prevent="simularPressed">
                <b-form-group label="ConteÃºdo da fita (palavra):">
                    <b-form-input autofocus v-model="modal.simular.palavra" trim autocomplete="off" />
                </b-form-group>
                <b-button type="submit" variant="primary" class="w-100">Simular</b-button>
            </b-form>
        </b-modal>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                items_map: new Map(),
                items: [
                ],
                inicial: null,
                finais: [],
                modal: {
                    novo_estado: {
                        nome: '',
                    },
                    definir_regex: {
                        regex: '',
                    },
                    estado_options: {
                        item: null,
                    },
                    simular: {
                        palavra: '',
                    },
                },
                error: null,
                resultado: null,
                afn_equivalente: null,
            }
        },
        mounted(){
            document.title = 'AFNe - Linguagens Formais';
        },
        methods: {
            loadAutomato(p){
                if(p == null)
                    return;

                const map = new Map();
                for(let item of p.items)
                    map.set(item['_'], item);

                this.items_map = map;

                for(let key in p)
                    this[key] = p[key];
            },
            novoEstado(){
                this.modal.novo_estado.nome = '';
                this.$refs['novo-estado'].show();
            },
            novoEstadoSave(){
                const novo = this.modal.novo_estado.nome;
                if(!novo)
                    return;

                /** @type {string[]} */
                const estados = this.items.map(x => x['_']);
                if(estados.includes(novo))
                    return;
                estados.push(novo);

                const items = [];

                const map = new Map();
                for(let item of this.items)
                    map.set(item['_'], item);

                for(let s1 of estados){
                    const obj = { '_': s1 };
                    for(let s2 of estados)
                        obj[s2] = map.has(s1) ? map.get(s1)[s2] || '' : '';
                    items.push(obj);
                }

                map.clear();
                for(let item of items)
                    map.set(item['_'], item);

                this.items_map = map;
                this.items = items;

                this.novoEstado();
            },
            definirViaRegex(){
                this.modal.definir_regex.regex = '';
                this.$refs['definir-regex'].show();
            },
            definirViaRegexSave(){
                try {
                    this.error = null;

                    this.$refs['definir-regex'].hide();
                    const str = this.modal.definir_regex.regex;

                    const ast = [ null ];
                    
                    let node = ast;
                    for(let i = 0; i < str.length; i++){
                        if(str[i] === '('){
                            let n = [ node ];
                            node.push(n);
                            node = n;
                        }
                        else if(str[i] === ')'){
                            node = node[0] === null ? ast : node[0];
                        }
                        else
                            node.push(str[i]);
                    }

                    let count = 0;
                    let maquina = { };

                    function gen(ast){
                        if(ast.length <= 1){
                            if(typeof(ast) === 'string')
                                return gen([null, ast]);
                            return [ null, null ];
                        }

                        const inicial = 'q' + (count++);
                        maquina[inicial] = { '_': inicial, 'Îµ': new Set() };

                        let cabeca = inicial;

                        for(let i = 2; i <= ast.length; i++){
                            switch(ast[i]){
                                case '+': {
                                    if(i >= ast.length - 1)
                                        throw new Error('Erro de sintaxe: o operador + precisa de dois argumentos.');

                                    const [ _inicial_1, _final_1 ] = gen(ast[i - 1]);
                                    const [ _inicial_2, _final_2 ] = gen(ast[i + 1]);

                                    const state = 'q' + (count++);
                                    maquina[state] = { '_': state, 'Îµ': new Set() };

                                    maquina[cabeca]['Îµ'].add(_inicial_1); // pluga 1
                                    maquina[cabeca]['Îµ'].add(_inicial_2); // pluga 2
                                    maquina[_final_1]['Îµ'].add(state); // saida do 1
                                    maquina[_final_2]['Îµ'].add(state); // saida do 2

                                    cabeca = state;
                                    i++; //impede que esse simbolo seja processado novamente
                                    i++; //impede que o segundo argumento seja processado novamente
                                    break;
                                }
                                case '*': {
                                    const [ _inicial, _final ] = gen(ast[i - 1]);

                                    const state = 'q' + (count++);
                                    maquina[state] = { '_': state, 'Îµ': new Set() };

                                    maquina[cabeca]['Îµ'].add(_inicial); // pluga
                                    maquina[_final]['Îµ'].add(_inicial); // loop
                                    maquina[_final]['Îµ'].add(state); // saida do loop
                                    maquina[cabeca]['Îµ'].add(state); // possibilidade de pular o loop

                                    cabeca = state;
                                    i++; //impede que esse simbolo seja processado novamente
                                    break;
                                }
                                case 'Îµ': {
                                    // palavra vazia
                                }
                                default: {
                                    const k = ast[i - 1];
                                    if(Array.isArray(k)){
                                        const [ _inicial, _final ] = gen(k);
                                        maquina[cabeca]['Îµ'].add(_inicial);

                                        cabeca = _final;
                                    }
                                    else {
                                        const s1 = 'q' + (count++);
                                        const s2 = 'q' + (count++);
                                        maquina[s1] = { '_': s1, 'Îµ': new Set(), [k]: new Set([ s2 ]) };
                                        maquina[s2] = { '_': s2, 'Îµ': new Set() };
                                        maquina[cabeca]['Îµ'].add(s1);

                                        cabeca = s2;
                                    }
                                    break;
                                }
                            }
                        }

                        const final = 'q' + (count++);
                        maquina[final] = { '_': final, 'Îµ': new Set() };
                        maquina[cabeca]['Îµ'].add(final);

                        return [ inicial, final ];
                    }

                    let [ inicial, final ] = gen(ast);
                    if(inicial == null){
                        maquina['q0'] = { '_': 'q0', 'Îµ': new Set() };
                        inicial = 'q0';
                    }
                    let finais = final ? [ final ] : [];

                    console.log(count, maquina, inicial, finais);

                    const items = this.converterMaquinaToItems(Object.values(maquina));
                    console.log(items);

                    this.loadAutomato({ inicial, finais, items });

                    this.error = null;
                }
                catch(ex){
                    console.error(ex);
                    this.error = ex.message;
                }
            },
            estadoOptions(item){
                this.modal.estado_options.item = item;
                this.$refs['estado-options'].show();
            },
            setEstadoInicial(item){
                this.inicial = item['_'];
                
                this.$refs['estado-options'].hide();
            },
            toggleEstadoFinal(item){
                const includes = this.finais.includes(item['_']);
                if(includes)
                    this.finais = this.finais.filter(x => x !== item['_']);
                else
                    this.finais.push(item['_']);

                this.$refs['estado-options'].hide();
            },
            simular(){
                this.error = null;

                try {
                    if(this.inicial == null)
                        throw new Error('Defina qual o estado inicial');

                    if(this.finais.length < 1)
                        throw new Error('Defina pelo menos um estado final');

                    this.modal.simular.palavra = '';
                    this.$refs['simular'].show();

                    this.error = null;
                }
                catch(ex){
                    this.resultado = null;
                    
                    console.error(ex);
                    this.error = ex.message;
                }
            },
            getMaquina(){
                const maquina = {};

                for(let item of this.items){
                    maquina[item['_']] = {};
                    for(let k in item){
                        if(k !== '_' && k.length > 0 && item[k].length > 0){
                            for(let t of item[k].split(',').map(x => x.trim())){
                                if(t.length > 0){
                                    if(maquina[item['_']][t] == null)
                                        maquina[item['_']][t] = [];
                                    maquina[item['_']][t].push(k);
                                }
                            }
                        }
                    }
                }

                const _maquina = JSON.parse(JSON.stringify(maquina));

                // retorna os estados acessiveis via transicao vazia (fecho Îµ)
                function computaAcessiveisViaE(state, resultado = new Set()){
                    const next_states = _maquina[state]['Îµ'];
                    if(next_states == null)
                        return resultado;
                    for(let next of next_states){
                        if(!resultado.has(next)){
                            resultado.add(next);
                            computaAcessiveisViaE(next, resultado);
                        }
                    }
                    return resultado;
                }

                // preprocessa a maquina fazendo o flatten das transiÃ§Ãµes vazias
                for(let k in maquina){
                    let next_states = Array.from(computaAcessiveisViaE(k));
                    for(let next of next_states){
                        if(next !== k){
                            for(let n in maquina[next]){
                                if(n !== '_' && n !== 'Îµ' && maquina[next][n] != null){
                                    if(maquina[k][n] == null)
                                        maquina[k][n] = [];

                                    for(let w of maquina[next][n]){
                                        if(!maquina[k][n].includes(w))
                                            maquina[k][n].push(w);
                                    }
                                }
                            }
                        }
                    }
                }

                // remove as transicoes vazias da maquina
                for(let k in maquina){
                    if('Îµ' in maquina[k])
                        delete maquina[k]['Îµ'];
                }

                return {
                    maquina,
                    computaAcessiveisViaE,
                };
            },
            simularPressed(){
                this.resultado = null;
                this.error = null;
                this.$refs['simular'].hide();

                try {
                    const { maquina, computaAcessiveisViaE } = this.getMaquina();

                    const palavra = this.modal.simular.palavra;
                    const caminhos = [];
                    
                    caminhos.push({ _i: caminhos.length, sequencia: [ { _i: 0, s: this.inicial, t: null } ], aceita: false, indefinido: null });

                    for(let c of palavra){
                        for(let caminho of Array.from(caminhos)){
                            if(caminho.indefinido)
                                continue;

                            const state = caminho.sequencia[caminho.sequencia.length - 1].s;
                            if(maquina[state][c] == null){
                                caminho.indefinido = { c };
                                continue;
                            }

                            const next_states = maquina[state][c];
                            const caminho_clone = JSON.parse(JSON.stringify(caminho));

                            for(let i = 0; i < next_states.length; i++){
                                const next_state = next_states[i];

                                const _caminho = i === 0 ? caminho : JSON.parse(JSON.stringify(caminho_clone));
                                _caminho.sequencia.push({ _i: _caminho.sequencia.length, s: next_state, t: c })

                                if(i > 0)
                                    caminhos.push(_caminho);
                            }
                        }
                    }

                    for(let caminho of caminhos){
                        if(caminho.indefinido)
                            continue;
                        const state = caminho.sequencia[caminho.sequencia.length - 1].s;
                        if(this.finais.includes(state)){
                            caminho.aceita = true;
                        }
                        else { //nao eh um estado final, mas um estado final eh acessivel via transicao vazia
                            const fecho_epsilon = computaAcessiveisViaE(state);
                            if(this.finais.find(x => fecho_epsilon.has(x)))
                                caminho.aceita = true;
                        }
                    }

                    this.resultado = {
                        palavra,
                        caminhos,
                        aceita: caminhos.find(x => x.aceita),
                        fecho_epsilon: this.items.map(x => ({ '_': x['_'], e: Array.from(computaAcessiveisViaE(x['_'])).sort() })),
                    };

                    this.error = null;
                }
                catch(ex){
                    console.error(ex);
                    this.error = ex.message;
                }
            },
            converterAFN(){
                try {
                    if(this.inicial == null)
                        throw new Error('Defina qual o estado inicial');

                    if(this.finais.length < 1)
                        throw new Error('Defina pelo menos um estado final');
                
                    const { maquina, computaAcessiveisViaE } = this.getMaquina();

                    const finais = new Set();
                    const afn = [];
                    for(let item of this.items){
                        const _item = JSON.parse(JSON.stringify(maquina[item['_']]));
                        _item['_'] = item['_'];

                        for(let k in _item){
                            if(k !== '_')
                                _item[k] = Array.from(_item[k]);
                        }
                        afn.push(_item);

                        // se um estado final eh acessivel via fecho epsilon,
                        // entao o estado corrente tbm eh final
                        const fecho_epsilon = computaAcessiveisViaE(item['_']);
                        for(let k of this.finais){
                            if(fecho_epsilon.has(k)){
                                finais.add(item['_']);
                                break;
                            }
                        }
                    }
                    
                    // adiciona os estados finais regulares
                    for(let k of this.finais)
                        finais.add(k);

                    this.afn_equivalente = {
                        inicial: this.inicial,
                        finais: Array.from(finais),
                        afn,
                        afn_texto: afn.map(x => `${x['_']}\t${Object.keys(x).filter(y => y !== '_').sort().map(y => `${y} -> ${x[y].join(',')}`).join('\t')}`),
                        afn_fields: ['__','_'].concat(Array.from(afn.flatMap(x => Object.keys(x)).filter(x => x !== '_').reduce((acc, x) => acc.add(x), new Set())).sort()),
                    };

                    console.log(this.afn_equivalente);
                        
                    this.error = null;
                }
                catch(ex){
                    this.resultado = null;
                    
                    console.error(ex);
                    this.error = ex.message;
                }
            },
            converterMaquinaToItems(maquina){
                const items = [];

                const estados = maquina.map(x => x['_']);
                for(let s of maquina){
                    const item = { '_': s['_'] };
                    for(let k in s){
                        if(k !== '_' && k.length > 0){
                            for(let el of s[k]){
                                if(item[el] == null)
                                    item[el] = new Set();
                                item[el].add(k);
                            }
                        }
                    }

                    // gera as propriedades que estao faltando
                    for(let _item of maquina)
                        item[_item['_']] = item[_item['_']] != null ? item[_item['_']] : new Set();

                    // reordena as propriedades
                    const _item = { '_': item['_'] };
                    for(let k of Object.keys(item).sort((a,b) => estados.indexOf(a) - estados.indexOf(b))){
                        if(k !== '_' && k.length > 0)
                            _item[k] = Array.from(item[k]).sort().join(','); //transforma os sets em string
                    }
                    
                    items.push(_item);
                }

                return items;
            },
            abrirAFN(){
                const payload = {
                    inicial: this.afn_equivalente.inicial,
                    finais: this.afn_equivalente.finais,
                    items: this.converterMaquinaToItems(this.afn_equivalente.afn),
                };

                window.open(this.$router.resolve({ path: 'afn', query: { p: JSON.stringify(payload) } }).href, '_blank');
            }
        }
    };
</script>

<style scoped>
